@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header{background-color:#3f3030;min-height:100vh}#inp{display:flex;flex-direction:column;align-items:center;justify-content:center;padding-top:5px}#inp textarea{width:99%;min-height:100px;margin-bottom:-5px;resize:vertical;padding-left:5px;padding-top:5px;font-size:12px;border-radius:5px;border-color:#0a883e}#opt{text-align:left;padding:0px 10px 0px 10px;color:blanchedalmond}h3{text-align:center;color:#57c592}#preview blockquote{border-left:3px solid #17cece;color:#bfdddd;padding-left:5px;margin-left:25px}#preview code{background:white;padding:1px 4px 2px 4px;font-size:12px;font-weight:bold}#preview pre{background:white;padding:5px 0 5px 5px}#preview h1{border-bottom:2px solid white}#preview h2{border-bottom:1px solid white}#preview td,#preview th{border:2px solid #2c0b7a;padding-left:5px;padding-right:5px}

